@import "colors";
@import "effects";
@import "typography";
@import "mixins";
@import "buttons";
@import "queries";
@import "inputs";
@import "grid";


html {
  box-sizing: border-box; }
*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: 'Arial', sans-serif; }

form {
  font-size: $base-font-size; }

.card-checkout {
  min-width: 320px;
  padding: 30px 15px;
  @media #{$tablet} {
    padding-right: 7px;
    padding-left: 7px; } }

.card-checkout__title {
  margin-bottom: 70px;
  text-align: center;
  font-weight: bold;
  line-height: 26px;
  font-size: $font-size-x-large;

  @media #{$tablet} {
    font-size: $font-size-xx-large; }

  @media #{$desktop} {
    font-size:  $font-size-xx-large; } }


.cf {
  @include clearfix; }

.label {
  display: block;
  margin: 6px 0;
  font-weight: bold; }

.list {
  width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  top: 104px;
  @media #{$tablet} {
    display: flex;
    justify-content: space-between;
    position: relative;
    top: 0; } }

.list {
  padding: 0;
  margin: 0;
  text-align: center;
  @media #{$tablet} {} }

.list__item {
  display: inline-block;
  margin-right: 20px;
  &:last-child {
    margin-right: 0; }
  @media #{$tablet} {
    margin-right: auto;
    margin-top: 3px;
    margin-bottom: -1px; } }

.list__item img {
  display: block;
  height: 30px;
  @media #{$tablet} {
    height: 27px; } }

.gateway__link {
  padding: 0;
  margin: 0;
  border: 0;
  cursor: pointer;
  width: 100px;
  height: 38px;
  align-self: flex-end; }
